import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { LANGUAGES } from "@constants/languages";
import { useDispatch } from "react-redux";

import ALERT_MESSAGE_TYPES from "@constants/alert-message-types";
import { SERVICE_STATES } from "@utils/hooks";
import { useCreateSnippet } from "@hooks/snippets";

import Box from "@material-ui/core/Box";
import { Paper, Typography } from "@material-ui/core";
import Chip from "@material-ui/core/Chip";
import SnippetDialog from "./snippet-dialog";
import ACTIONS from "@redux/actions";

const useStyles = makeStyles((theme) => {
  return {
    root: {
      padding: 10,
      marginTop: 50,
      marginBottom: 30,
    },
    languageChip: {
      textAlign: "left",
      minWidth: 100,
      margin: 5,
      color: "white",
    },
  };
});

export default function CreateSnippet({
  defaultLanguage = "JavaScript",
  alertCallback,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = React.useState(false);
  const [language, setLanguage] = React.useState(defaultLanguage);
  const [title, setTitle] = React.useState("My snippet");
  const [description, setDescription] = React.useState("");
  const [content, setContent] = React.useState("");
  const [cursor, setCursor] = React.useState({
    ch: 0,
    line: 0,
  });
  const {
    createSnippetTrigger,
    createSnippetStatus,
    createSnippetData,
  } = useCreateSnippet();

  useEffect(() => {
    if (createSnippetStatus === SERVICE_STATES.SUCCESS) {
      console.log(createSnippetData);
      dispatch({
        type: ACTIONS.CREATE_SNIPPET,
        value: {
          snippet: createSnippetData,
        },
      });
      alertCallback({
        message: "Snippet created",
        type: ALERT_MESSAGE_TYPES.SUCCESS,
      });
    }
  }, [createSnippetStatus]);

  const handleClickOpen = ({ languageKey }) => {
    console.log(languageKey);
    setLanguage(languageKey);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setTitle("My snippet");
    setDescription("");
    setContent("");
  };

  function handleCreateSnippet() {
    createSnippetTrigger({
      language,
      title,
      description,
      content,
    });
    handleClose();
  }

  return (
    <Paper className={classes.root}>
      <Typography variant="h5">Create a snippet</Typography>
      <br />
      <Box className={classes.language}>
        {Object.keys(LANGUAGES).map((languageKey) => {
          return (
            <Chip
              key={languageKey}
              onClick={() =>
                handleClickOpen({
                  languageKey,
                })
              }
              className={classes.languageChip}
              style={{
                backgroundColor: LANGUAGES[languageKey].color.primary,
              }}
              label={<Typography>{languageKey}</Typography>}
            />
          );
        })}
      </Box>
      <br />
      <SnippetDialog
        isUser={true}
        open={open}
        setOpen={setOpen}
        cursor={cursor}
        setCursor={setCursor}
        language={language}
        setLanguage={setLanguage}
        title={title}
        setTitle={setTitle}
        description={description}
        setDescription={setDescription}
        content={content}
        setContent={setContent}
        handleCreateSnippet={handleCreateSnippet}
      />
    </Paper>
  );
}
