import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import { Typography } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import { useGetDeveloper } from "@hooks/users";
import { SERVICE_STATES } from "@utils/hooks";

import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 50,
    textAlign: "center",
  },
  paperRoot: {
    padding: 20,
    width: "100%",
  },
  avatar: {
    width: "50%",
    borderRadius: "50%",
  },
  InfoItem: {
    "&:hover": {
      backgroundColor: theme.palette.grey[200],
    },
  },
}));

export default function DeveloperCard({ developerId }) {
  const classes = useStyles();
  const [developer, setDeveloper] = useState(null);

  const { user } = useSelector((state) => {
    return {
      user: state.profile.user,
    };
  });

  const {
    getDeveloperTrigger,
    getDeveloperStatus,
    getDeveloperData,
  } = useGetDeveloper();

  useEffect(() => {
    if (user.login === developerId) {
      setDeveloper(user);
    } else {
      getDeveloperTrigger({ developerId });
    }
  }, [developerId]);

  useEffect(() => {
    if (getDeveloperStatus === SERVICE_STATES.SUCCESS) {
      setDeveloper(getDeveloperData);
    }
  }, [getDeveloperStatus]);

  return (
    <div className={classes.root}>
      {developer && (
        <Paper className={classes.paperRoot}>
          <img
            alt={developer.name}
            src={developer.avatar_url}
            className={classes.avatar}
          />

          <Typography variant="subtitle1">
            <strong>{developer.name}</strong>
          </Typography>
          {developer.bio && (
            <Typography variant="body2">{developer.bio}</Typography>
          )}
          <br />
          <Link
            to={"/developer/" + developerId + "/followers"}
            style={{
              textDecoration: "none",
              color: "black",
            }}
          >
            <Typography className={classes.InfoItem} variant="body1">
              <strong>{developer.followers}</strong> followers
            </Typography>
          </Link>
          <Link
            to={"/developer/" + developerId + "/following"}
            style={{
              textDecoration: "none",
              color: "black",
            }}
          >
            <Typography className={classes.InfoItem} variant="body1">
              <strong>{developer.following}</strong> following
            </Typography>
          </Link>
          <br />
          {developer.company && (
            <Typography variant="subTitle2">
              Works at <strong>{developer.company}</strong>
            </Typography>
          )}
        </Paper>
      )}
    </div>
  );
}
