import React from "react";
import { HashRouter as Router, Switch } from "react-router-dom";
import Login from "@auth/login";
import Developer from "@developer";
import SecureRoute from "@auth/secure-route";
import AuthRoute from "@auth/auth-route";
import Auth from "@auth/auth";

const App = () => {
  return (
    <Router>
      <Switch>
        <AuthRoute exact path="/" component={Login} />
        <AuthRoute path="/auth" component={Auth} />

        <SecureRoute
          exact
          path="/developer/:developerId"
          component={() => <Developer page="root" />}
        />

        <SecureRoute
          exact
          path="/developer/:developerId/snippets"
          component={() => <Developer page="snippets" />}
        />

        <SecureRoute
          exact
          path="/developer/:developerId/following"
          component={() => <Developer page="following" />}
        />

        <SecureRoute
          exact
          path="/developer/:developerId/followers"
          component={() => <Developer page="followers" />}
        />
      </Switch>
    </Router>
  );
};

export default App;
