import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import ACTIONS from "@redux/actions";
import axios from "axios";
import { client_id } from "@constants/auth";
import { BASE_URL, APP_URL } from "@constants/url";

export default function Auth() {
  const dispatch = useDispatch();
  const [userAccessToken, setUserAccessToken] = useState(null);

  const { user } = useSelector((state) => {
    return {
      user: state.profile.user,
    };
  });

  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const code = urlParams.get("code");

  function successCallback({ userAccessToken, user }) {
    dispatch({
      type: ACTIONS.LOAD_PROFILE,
      value: { accessToken: userAccessToken, user },
    });
    window.location.href = APP_URL + "/developer/" + user.login;
  }

  function failureCallback(error) {
    console.log("Failed to authenticate-", error);
  }

  useEffect(() => {
    if (code) {
      axios
        .get(`${BASE_URL}/authenticate/${code}`)
        .then(function (response) {
          const accessToken = response.data.token;
          if (accessToken) {
            setUserAccessToken(accessToken);
          } else {
            failureCallback(response.data.error);
          }
        })
        .catch(function (error) {
          console.log("failed", error);
        });
    } else {
      window.location.href = `https://github.com/login/oauth/authorize?client_id=${client_id}&scope=gist`;
    }
  }, [code]);

  useEffect(() => {
    if (userAccessToken) {
      axios
        .get(`${BASE_URL}/user`, {
          params: {
            accessToken: userAccessToken,
          },
          headers: {
            Accept: "application/json",
          },
        })
        .then((response) => {
          successCallback({
            userAccessToken,
            user: response.data,
          });
        })
        .catch((error) => {});
    }
  }, [userAccessToken]);

  useEffect(() => {
    if (userAccessToken && user) {
      window.location.href = APP_URL + "/developer/" + user.login;
    }
  }, [userAccessToken, user]);

  return <div></div>;
}
