import Cookie from "js-cookie";

import ACTIONS from "./actions";
import INITIAL_STATE from "./initial-state";
import { LANGUAGES } from "@constants/languages";
import initialState from "./initial-state";

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case ACTIONS.LOAD_FOLLOWING_DEVELOPERS: {
      const { following } = action.value;
      return {
        ...state,
        developers: {
          ...state.developers,
          following: [...state.developers.following, ...following],
        },
      };
    }

    case ACTIONS.LOAD_FOLLOWERS_DEVELOPERS: {
      const { followers } = action.value;
      return {
        ...state,
        developers: {
          ...state.developers,
          followers: [...state.developers.followers, ...followers],
        },
      };
    }

    case ACTIONS.UNLOAD_FOLLOWING_DEVELOPERS: {
      return {
        ...state,
        developers: {
          ...state.developers,
          following: initialState.developers.following,
        },
      };
    }

    case ACTIONS.UNLOAD_FOLLOWERS_DEVELOPERS: {
      return {
        ...state,
        developers: {
          ...state.developers,
          followers: initialState.developers.followers,
        },
      };
    }

    case ACTIONS.UNLOAD_DEVELOPER: {
      return {
        ...state,
        developers: initialState.developers,
        snippets: initialState.snippets,
      };
    }

    case ACTIONS.LOAD_PROFILE: {
      const { accessToken, user } = action.value;
      Cookie.set("access_token", accessToken);
      Cookie.set("user", user);
      return {
        ...state,
        profile: action.value,
      };
    }

    case ACTIONS.UNLOAD_SNIPPETS: {
      return {
        ...state,
        snippets: initialState.snippets,
      };
    }

    case ACTIONS.LOAD_SNIPPETS: {
      const allSnippets = action.value;
      let filteredSnippets = allSnippets.filter((snippet) => {
        const fileKey = Object.keys(snippet.files)[0];
        if (Object.keys(LANGUAGES).includes(snippet.files[fileKey].language))
          return snippet;
      });

      let processedSnippets = filteredSnippets.map((snippet) => {
        const fileKey = Object.keys(snippet.files)[0];
        const fileName = snippet.files[fileKey].filename;
        const language = snippet.files[fileKey].language;
        return {
          snippetId: snippet.id,
          language,
          title: fileName.substring(
            0,
            fileName.length - LANGUAGES[language].extension.length
          ),
          description: snippet.description,
          rawUrl: snippet.files[fileKey].raw_url,
          content: null,
        };
      });
      const newState = {
        ...state,
        snippets: [...state.snippets, ...processedSnippets],
      };
      return newState;
    }

    case ACTIONS.CREATE_SNIPPET:
      const { snippet } = action.value;

      const fileKey = Object.keys(snippet.files)[0];
      const fileName = snippet.files[fileKey].filename;
      const language = snippet.files[fileKey].language;

      const file = {
        snippetId: snippet.id,
        language,
        title: fileName.substring(
          0,
          fileName.length - LANGUAGES[language].extension.length
        ),
        description: snippet.description,
        rawUrl: snippet.files[fileKey].raw_url,
        content: null,
      };

      return {
        ...state,
        snippets: [file, ...state.snippets],
      };
      break;

    case ACTIONS.UPDATE_SNIPPET: {
      const { description, content, snippetId } = action.value;
      const newSnippets = state.snippets.map((snippet) => {
        if (snippet.snippetId == snippetId) {
          return {
            ...snippet,
            description,
            content,
          };
        } else {
          return snippet;
        }
      });
      return {
        ...state,
        snippets: newSnippets,
      };
    }

    case ACTIONS.DELETE_SNIPPET: {
      const newSnippets = state.snippets.filter(
        (snippet) => snippet.snippetId != action.value.snippetId
      );
      return {
        ...state,
        snippets: newSnippets,
      };
    }

    default:
      return state;
  }
}

export default reducer;
