import React, { useState, useEffect } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { CopyToClipboard } from "react-copy-to-clipboard";
import ALERT_MESSAGE_TYPES from "@constants/alert-message-types";
import { SERVICE_STATES } from "@utils/hooks";
import ACTIONS from "@redux/actions";
import { useSelector, useDispatch } from "react-redux";

import {
  useDeleteSnippet,
  useLoadSnippet,
  useUpdateSnippet,
} from "@hooks/snippets";

import { Controlled as CodeMirror } from "react-codemirror2";

import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import LinkIcon from "@material-ui/icons/Link";
import OpenInNewIcon from "@material-ui/icons/OpenInNew";
import Typography from "@material-ui/core/Typography";
import Chip from "@material-ui/core/Chip";
import { Box, Divider } from "@material-ui/core";
import Tooltip from "@material-ui/core/Tooltip";

import { LANGUAGES } from "@constants/languages";
import SnippetDialog from "./snippet-dialog";
import DialogMessage from "@components/dialog-message";
import AlertMessage from "@components/alert-message";

const HtmlTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: "#f5f5f9",
    color: "rgba(0, 0, 0, 0.87)",
    maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: "1px solid #dadde9",
  },
}))(Tooltip);

const useStyles = makeStyles((theme) => {
  return {
    root: {
      display: "inline-block",
      width: 250,
      margin: 15,
      textAlign: "left",
      userSelect: "none",
    },
    snippetCard: {
      zIndex: 1,
    },
    snippetContent: {
      height: 360,
    },
    snippetFileContent: {
      fontSize: 14,
      height: 200,
      wordWrap: "break-word",
      overflow: "hidden",
      textOverflow: "ellipsis",
    },
    language: {
      textAlign: "center",
      marginBottom: -12,
      marginRight: -10,
    },
    languageChip: {
      minWidth: 100,
      border: "1px solid lightgray",
      color: "white",
    },
    title: {
      textOverflow: "ellipsis",
      overflow: "hidden",
    },
    description: {
      marginBottom: 12,
      textOverflow: "ellipsis",
      overflow: "hidden",
    },
    snippetContent: {},
    snippetActionIcon: {},
    deleteActionIcon: {},
  };
});

export default function SnippetCard({
  content,
  snippetId,
  language,
  title,
  description,
  rawUrl,
  isUser,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [fileContent, setFileContent] = useState(content);
  const [alertObj, setAlertObj] = useState(false);

  const [updateDialogOpen, setUpdateDialogOpen] = useState(false);
  const [dialogLanguage, setDialogLanguage] = React.useState(language);
  const [dialogTitle, setDialogTitle] = React.useState(title);
  const [dialogDescription, setDialogDescription] = React.useState(description);
  const [dialogContent, setDialogContent] = React.useState(content);
  const [dialogCursor, setDialogCursor] = React.useState({
    ch: 0,
    line: 0,
  });

  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

  const { deleteSnippetTrigger, deleteSnippetStatus } = useDeleteSnippet();
  const { updateSnippetTrigger, updateSnippetStatus } = useUpdateSnippet();

  const {
    loadSnippetTrigger,
    loadSnippetStatus,
    loadSnippetData,
  } = useLoadSnippet();

  useEffect(() => {
    if (!content) {
      loadSnippetTrigger({
        rawUrl,
      });
    } else {
      setFileContent(content);
    }
  }, [content]);

  useEffect(() => {
    if (loadSnippetStatus === SERVICE_STATES.SUCCESS) {
      setFileContent(loadSnippetData);
      setDialogContent(loadSnippetData);
    }
  }, [loadSnippetStatus]);

  useEffect(() => {
    if (updateSnippetStatus === SERVICE_STATES.SUCCESS) {
      setAlertObj({
        message: "Snippet updated",
        type: ALERT_MESSAGE_TYPES.INFO,
      });
      updateSnippet({
        snippetId,
        description: dialogDescription,
        content: dialogContent,
      });
    }
  }, [updateSnippetStatus]);

  useEffect(() => {
    if (deleteSnippetStatus === SERVICE_STATES.SUCCESS) {
      setAlertObj({
        message: "Snippet deleted",
        type: ALERT_MESSAGE_TYPES.ERROR,
      });
      deleteSnippet({
        snippetId,
      });
    }
  }, [deleteSnippetStatus]);

  function handleCopyLink() {
    setAlertObj({
      message: "Copied to clipboard!",
      type: ALERT_MESSAGE_TYPES.INFO,
    });
  }

  function handleDeleteSnippetIconClick() {
    setDeleteDialogOpen(true);
  }

  function handleDeleteSnippet(snippetId) {
    setDeleteDialogOpen(false);
    deleteSnippetTrigger({
      snippetId,
    });
  }

  function handleUpdateSnippet({
    snippetId,
    language,
    title,
    description,
    content,
  }) {
    updateSnippetTrigger({
      snippetId,
      language,
      title,
      description,
      content,
    });

    setUpdateDialogOpen(false);
  }

  function deleteSnippet({ snippetId }) {
    dispatch({
      type: ACTIONS.DELETE_SNIPPET,
      value: { snippetId },
    });
  }

  function updateSnippet({ snippetId, description, content }) {
    dispatch({
      type: ACTIONS.UPDATE_SNIPPET,
      value: { snippetId, description, content },
    });
  }

  function handleCloseAlert() {
    setUpdateDialogOpen(false);
  }

  return (
    <div className={classes.root}>
      <Box className={classes.language}>
        <Chip
          className={classes.languageChip}
          style={{
            backgroundColor: LANGUAGES[language].color.primary,
          }}
          size="small"
          label={language ? language : "Unknown Language"}
        />
        <HtmlTooltip
          placement="top"
          title={
            <React.Fragment>
              <Typography>Open in a new tab</Typography>
            </React.Fragment>
          }
        >
          <IconButton
            target="_blank"
            href={rawUrl}
            size="small"
            aria-label="Open in new tab"
          >
            <OpenInNewIcon
              style={{
                marginRight: -133,
                marginBottom: -5,
                backgroundColor: "white",
              }}
              className={classes.DeleteActionIcon}
            />
          </IconButton>
        </HtmlTooltip>
      </Box>
      <Card
        className={classes.snippetCard}
        style={{
          border: `1px solid ${LANGUAGES[language].color.primary}`,
        }}
        variant="outlined"
      >
        <CardContent className={classes.snippetContent}>
          <HtmlTooltip title={<React.Fragment>{title}</React.Fragment>}>
            <Box className={classes.title}>
              <Typography noWrap variant="body1">
                {title}
              </Typography>
            </Box>
          </HtmlTooltip>
          <HtmlTooltip title={<React.Fragment>{description}</React.Fragment>}>
            <Box className={classes.description}>
              <Typography noWrap variant="caption" color="textSecondary">
                {description || "No description added"}
              </Typography>
            </Box>
          </HtmlTooltip>

          <Divider />
          <br />
          <div className={classes.snippetFileContent}>
            <CodeMirror
              value={fileContent}
              options={{
                mode: LANGUAGES[language].mode || "javascript",
              }}
            />
          </div>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            onClick={() => setUpdateDialogOpen(true)}
            variant="outlined"
            size="small"
          >
            Open
          </Button>

          <HtmlTooltip
            placement="top"
            title={
              <React.Fragment>
                <Typography>Copy link to clipboard</Typography>
              </React.Fragment>
            }
          >
            <CopyToClipboard text={rawUrl} onCopy={handleCopyLink}>
              <IconButton size="small" aria-label="copy link">
                <LinkIcon className={classes.snippetActionIcon} />
              </IconButton>
            </CopyToClipboard>
          </HtmlTooltip>

          <div style={{ flex: 1 }}></div>
          {isUser && (
            <HtmlTooltip
              placement="top"
              title={
                <React.Fragment>
                  <Typography>Delete snippet</Typography>
                </React.Fragment>
              }
            >
              <IconButton
                size="small"
                onClick={handleDeleteSnippetIconClick}
                aria-label="delete"
              >
                <DeleteIcon className={classes.deleteActionIcon} />
              </IconButton>
            </HtmlTooltip>
          )}
        </CardActions>
      </Card>
      {updateDialogOpen && (
        <SnippetDialog
          isUser={isUser}
          snippetId={snippetId}
          open={updateDialogOpen}
          setOpen={setUpdateDialogOpen}
          cursor={dialogCursor}
          setCursor={setDialogCursor}
          language={dialogLanguage}
          setLanguage={setDialogLanguage}
          title={dialogTitle}
          setTitle={setDialogTitle}
          description={dialogDescription}
          setDescription={setDialogDescription}
          content={dialogContent}
          setContent={setDialogContent}
          handleUpdateSnippet={handleUpdateSnippet}
        />
      )}
      {deleteDialogOpen && (
        <DialogMessage
          dialogOpen={deleteDialogOpen}
          setDialogOpen={setDeleteDialogOpen}
          rightButtontext="Delete"
          leftButtonText="Close"
          onRightButtonClick={() => handleDeleteSnippet(snippetId)}
          rightButtonColor="red"
          onLeftButtonClick={() => {
            setDeleteDialogOpen(false);
          }}
          titleText="Delete snippet"
          bodyText="Deleted snippets can never be recovered again. Are you sure you want to lose your work ?"
        />
      )}
      {alertObj && (
        <AlertMessage handleCloseAlert={handleCloseAlert} {...alertObj} />
      )}
    </div>
  );
}
