import axios from "axios";
import Cookies from "js-cookie";
import { LANGUAGES } from "@constants/languages";

import { useAsync } from "@utils/hooks";
import { BASE_URL } from "@constants/url";

export const UseGetDeveloperSnippets = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({ developerId, page }) =>
      axios.get(BASE_URL + "/developerSnippets/" + developerId, {
        params: {
          developerId,
          page,
        },
        headers: {
          Accept: "application/json",
        },
      })
  );

  return {
    getDeveloperSnippetsTrigger: exec,
    isGetDeveloperSnippetsInProgress: inProgress,
    getDeveloperSnippetsStatus: status,
    getDeveloperSnippetsData: data,
    getDeveloperSnippetsError: error,
  };
};
export const useGetSnippets = () => {
  const { exec, inProgress, error, status, data } = useAsync(async ({ page }) =>
    axios.get(BASE_URL + "/snippets", {
      params: {
        accessToken: Cookies.get("access_token"),
        page,
      },
      headers: {
        Accept: "application/json",
      },
    })
  );

  return {
    getSnippetsTrigger: exec,
    isGetSnippetsInProgress: inProgress,
    getSnippetsStatus: status,
    getSnippetsData: data,
    getSnippetsError: error,
  };
};

export const useLoadSnippet = () => {
  let { exec, inProgress, error, status, data } = useAsync(async ({ rawUrl }) =>
    axios.get(BASE_URL + "/loadSnippet", {
      params: {
        accessToken: Cookies.get("access_token"),
        rawUrl,
      },
      headers: {
        Accept: "application/json",
      },
    })
  );

  if (typeof data === "object") {
    data = JSON.stringify(data, undefined, 2);
  }

  return {
    loadSnippetTrigger: exec,
    isLoadSnippetInProgress: inProgress,
    loadSnippetStatus: status,
    loadSnippetData: data,
    loadSnippetError: error,
  };
};

export const useCreateSnippet = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({ language, title, description, content, secret = false }) => {
      const fileName = `${title}${LANGUAGES[language].extension}`;
      return axios.post(
        BASE_URL + "/createSnippet",
        {
          description,
          files: {
            [fileName]: {
              content,
            },
          },
          public: !secret,
        },
        {
          params: {
            accessToken: Cookies.get("access_token"),
          },
          headers: {
            Accept: "application/json",
          },
        }
      );
    }
  );

  return {
    createSnippetTrigger: exec,
    isCreateSnippetInProgress: inProgress,
    createSnippetStatus: status,
    createSnippetData: data,
    createSnippetError: error,
  };
};

export const useUpdateSnippet = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({
      snippetId,
      language,
      title,
      description,
      content,
      secret = false,
    }) => {
      const fileName = `${title}${LANGUAGES[language].extension}`;
      return axios.patch(
        `${BASE_URL}/updateSnippet/${snippetId}`,
        {
          description,
          files: {
            [fileName]: {
              content,
            },
          },
          public: !secret,
        },
        {
          params: {
            accessToken: Cookies.get("access_token"),
          },
          headers: {
            Accept: "application/json",
          },
        }
      );
    }
  );

  return {
    updateSnippetTrigger: exec,
    isUpdateSnippetInProgress: inProgress,
    updateSnippetStatus: status,
    updateSnippetData: data,
    updateSnippetError: error,
  };
};

export const useDeleteSnippet = () => {
  const { exec, inProgress, error, status } = useAsync(async ({ snippetId }) =>
    axios.delete(`${BASE_URL}/deleteSnippet/${snippetId}`, {
      params: {
        accessToken: Cookies.get("access_token"),
      },
      headers: {
        Accept: "application/json",
      },
    })
  );

  return {
    deleteSnippetTrigger: exec,
    isDeleteSnippetInProgress: inProgress,
    deleteSnippetStatus: status,
    deleteSnippetError: error,
  };
};
