import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Box } from "@material-ui/core";
import SnippetCard from "@components/snippet-card";
import NoView from "@components/no-view";
import { useSelector, useDispatch } from "react-redux";
import { SERVICE_STATES } from "@utils/hooks";
import { useGetSnippets, UseGetDeveloperSnippets } from "@hooks/snippets";
import ACTIONS from "@redux/actions";
import VizSensor from "react-visibility-sensor";
import SnippetCardSkeleton from "@components/skeletons/snippet-card-skeleton";
import LinearProgress from "@material-ui/core/LinearProgress";

const useStyles = makeStyles((theme) => {
  return {
    root: {
      marginTop: 20,
    },
  };
});

export default function Snippets({ developerId, isUser, feed = false }) {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [page, setPage] = useState(1);
  const [loadingComplete, setLoadingComplete] = useState(false);

  const { snippets, userId } = useSelector((state) => {
    return {
      snippets: state.snippets,
      userId: state.profile.user.login,
    };
  });

  const {
    getSnippetsTrigger,
    getSnippetsStatus,
    getSnippetsData,
    isGetSnippetsInProgress,
  } = useGetSnippets();
  const {
    getDeveloperSnippetsTrigger,
    getDeveloperSnippetsStatus,
    getDeveloperSnippetsData,
  } = UseGetDeveloperSnippets();

  useEffect(() => {
    if (userId === developerId) {
      getSnippetsTrigger({ page });
    } else {
      getDeveloperSnippetsTrigger({ developerId, page });
    }
  }, [page, developerId]);

  function loadMoreSnippets(isVisible) {
    if (!isGetSnippetsInProgress && isVisible) {
      setPage(page + 1);
    }
  }

  useEffect(() => {
    if (getSnippetsStatus === SERVICE_STATES.SUCCESS) {
      if (getSnippetsData.length && !loadingComplete) {
        if (Array.isArray(getSnippetsData)) {
          dispatch({
            type: ACTIONS.LOAD_SNIPPETS,
            value: getSnippetsData,
          });
        }
      } else {
        setLoadingComplete(true);
      }
    }
  }, [getSnippetsStatus]);

  useEffect(() => {
    if (getDeveloperSnippetsStatus === SERVICE_STATES.SUCCESS) {
      if (getDeveloperSnippetsData.length && !loadingComplete) {
        if (Array.isArray(getDeveloperSnippetsData)) {
          dispatch({
            type: ACTIONS.LOAD_SNIPPETS,
            value: getDeveloperSnippetsData,
          });
        }
      } else {
        setLoadingComplete(true);
      }
    }
  }, [getDeveloperSnippetsStatus]);

  return (
    <Box className={classes.root}>
      {snippets.length > 0 && (
        <Box className={classes.snippetsArea}>
          {snippets.map((snippet, index) => {
            return (
              <Box
                key={snippet.snippetId + "Ad" + index}
                style={{ display: "inline-block" }}
              >
                <SnippetCard isUser={isUser} {...snippet} />
              </Box>
            );
          })}
          {!loadingComplete && (
            <div>
              <VizSensor
                onChange={(isVisible) => {
                  loadMoreSnippets(isVisible);
                }}
              >
                <Box>
                  <br />
                  <LinearProgress />
                  <br />
                </Box>
              </VizSensor>
            </div>
          )}
        </Box>
      )}

      {loadingComplete && snippets.length === 0 && (
        <NoView
          primaryText="No snippets"
          secondaryText="Developer has not created any snippets yet."
        />
      )}
    </Box>
  );
}
