import axios from "axios";
import Cookies from "js-cookie";

import { useAsync } from "@utils/hooks";
import { BASE_URL } from "@constants/url";

export const useGetDeveloper = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({ developerId }) =>
      axios.get(BASE_URL + "/developer/" + developerId, {
        params: {
          accessToken: Cookies.get("access_token"),
        },
        headers: {
          Accept: "application/json",
        },
      })
  );

  return {
    getDeveloperTrigger: exec,
    isGetDeveloperInProgress: inProgress,
    getDeveloperStatus: status,
    getDeveloperData: data,
    getDeveloperError: error,
  };
};

export const useGetDevelopers = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({ developerId, type, page }) =>
      axios.get(
        BASE_URL + "/developers/" + developerId + "/followType/" + type,
        {
          params: {
            accessToken: Cookies.get("access_token"),
            page,
          },
          headers: {
            Accept: "application/json",
          },
        }
      )
  );

  return {
    getDevelopersTrigger: exec,
    isGetDevelopersInProgress: inProgress,
    getDevelopersStatus: status,
    getDevelopersData: data,
    getDevelopersError: error,
  };
};
