import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Typography } from "@material-ui/core";

export default function DialogMessage({
  dialogOpen = false,
  setDialogOpen,
  rightButtontext,
  rightButtonColor = "rgb(0%, 37.2%, 76.1%)",
  leftButtonText,
  onRightButtonClick,
  onLeftButtonClick,
  titleText,
  bodyText,
}) {
  const handleClose = () => {
    setDialogOpen(false);
  };

  return (
    <div>
      <Dialog open={dialogOpen} onClose={handleClose}>
        <DialogTitle>
          <Typography variant="h6">{titleText}</Typography>
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Typography variant="body1">{bodyText}</Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={onLeftButtonClick} color="default">
            <Typography>{leftButtonText}</Typography>
          </Button>
          <Button
            variant="contained"
            onClick={onRightButtonClick}
            style={{ backgroundColor: rightButtonColor, color: "white" }}
            autoFocus
          >
            <Typography>{rightButtontext}</Typography>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
