import { useState } from "react";

export const SERVICE_STATES = {
  IDLE: "IDLE",
  SUCCESS: "SUCCESS",
  FAILED: "FAILED",
  IN_PROGRESS: "IN_PROGRESS",
};

export const useAsync = (fetcher) => {
  const [resp, setResp] = useState({
    data: null,
    error: null,
    status: SERVICE_STATES.IDLE,
  });

  return {
    ...resp,
    inProgress: resp.status === SERVICE_STATES.IN_PROGRESS,
    exec: (...args) => {
      (async () => {
        setResp({
          data: null,
          error: null,
          status: SERVICE_STATES.IN_PROGRESS,
        });
        try {
          const data = await fetcher(...args);
          setResp({
            data: data.data,
            error: null,
            status: SERVICE_STATES.SUCCESS,
          });
        } catch (err) {
          setResp({
            data: null,
            error: err,
            status: SERVICE_STATES.FAILED,
          });
        }
      })();
    },
    reset: () => {
      setResp({
        data: null,
        error: null,
        status: SERVICE_STATES.IDLE,
      });
    },
  };
};
